:root {
  --color-accent1: #222831;
  --color-accent2: #FFD369;
  --color-accent3: #EEEEEE;
 --color-accent4: #FFD369;
  --color-red: #FFD369;
  --color-yellow: #ffd15c;
  --color-purpel: #393E46;
  --color-light-purpel: #393E46;
  --color-lilac: #EEEEEE;
  --border: solid 1px rgba(255, 255, 255, 0.1);
  --border2: solid 1px rgba(255, 255, 255, 0.1);
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto;
  direction: ltr;
}
body{
  font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: white;
    text-align: left;
    background-color: var(--color-accent1) !important;
}
img{
  width: 100%;
  height: 100%;
}