.services-pic{
  height: 6rem;
}
.service-box:hover {
  transform: translateY(-10px);
}
.service-box {
  transform: translateY(0);
  transition: all 0.3s ease-in-out;
}
.shadow-dark {
  box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1) !important;
}
.shadow-light {
  box-shadow: 0px 5px 20px 0px rgba(255, 255, 255, 0.01) !important;
}
.shadow-blue {
  box-shadow: 0px 5px 20px 0px rgba(255,211,105, 0.5) !important;
}
.shadow-pink {
  box-shadow: 0px 5px 20px 0px rgba(255,211,105, 0.5) !important;
}
.shadow-yellow {
  box-shadow: 0px 5px 20px 0px rgba( 238 , 238 , 238 , 0.5) !important;
}
.bg-blue {
  background-color: var(--color-accent2) !important;
}
.bg-yellow {
  background-color: var(--color-accent3) !important;
}
.bg-pink {
  background-color: var(--color-accent4) !important;
}
.portfolio-pic{
  border-radius: 100%;
  width: 12rem;
  height: 12rem;
  object-fit: contain;
}
.aboutME-box:before {
  content: "";
  width: 0;
  height: 0;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-right: 30px solid var(--color-purpel);
  position: absolute;
  left: -1.5rem;
  top: 4rem;
}
.experience-box::before{
  content: "";
  width: 0.1rem;
  height: 82%;
  background: var(--color-red);
  position: absolute;
  left: 1.5rem;
}
.recent-work-pic{
  object-fit: cover;
  border-radius: 1rem;
}
.recent-work-box:hover img {
  opacity: 0.3;
}
.recent-work-box:hover a {
  opacity: 1;
  color: white;
    text-decoration: none;
    font-size: 1.5rem;
}
.social-link {
  color: white;
}
.contact-btn , .cv-btn{
  color:  var(--color-accent1);
  text-decoration: none;
}
.contact-btn:hover , .cv-btn:hover{
  color: white;
}
.social-link:hover{
  color:  var(--color-accent2)
}
@media (max-width: 767px) {
  .aboutME-box:before {
    left: 9rem !important;
    top: unset !important;
    transform: rotate(269deg);
    bottom: -2rem;
  }
}